.modal {
	color: #ffffff;
	position: fixed;
	top: 55px;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.8);
	z-index: 1;
}

.display-block {
	display: block;
}

.display-none {
	display: none;
}

/* modal-box */

.modal-background {
	display: none;
}

.back-btn {
	text-align: right;
	background: #f00;
	color: #00f;
	border: none;
	float: right;
}

.modal .modal-main {
	position: relative;
	background-color: #3caf8e;
	width: 74%;
	margin-left: 13%;
	height: 100%;
	overflow-y: scroll;
	overflow-x: hidden;
	z-index: 1;
}

